import VueDragResize from 'vue-drag-resize'
import { createNamespacedHelpers } from 'vuex'
const { mapGetters } = createNamespacedHelpers('KnowledgeBase')
export default {
  name: 'DragResize',
  components: { VueDragResize },
  props: {
    maximized: {
      type: Boolean,
      required: true
    },
    coordinates: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      active: false
    }
  },
  methods: {
    resize(newRect) {
      this.$emit('onChangeCoordinates', {
        width: newRect.width,
        height: newRect.height,
        top: newRect.top,
        left: newRect.left
      })
    },
    onActivated() {
      $(this.$refs.resize.$el)
        .find('.vdr-stick')
        .addClass('mdi mdi-resize-bottom-right q-btn q-btn--dense q-btn--round q-icon contrast-50')
    },
    onFocusElement(event) {
      event.target.focus()
    }
  },
  mounted() {
    this.active = true
  }
}
